@media only screen and (min-width: 480px) {
  body {
    font-family: Montserrat;
  }
  body header {
    display: flex;
    width: 100%;
  }
  body header .conteneur-header-logo-nav {
    display: flex;
    padding: 20px;
    width: 100%;
  }
  body header .conteneur-header-logo-nav .kasaLogo {
    width: 50%;
    height: 63px;
  }
  body header .conteneur-header-logo-nav .nav-header {
    display: flex;
    gap: 0;
    width: 50%;
    align-items: center;
    justify-content: space-around;
  }
  body header .conteneur-header-logo-nav .nav-header a {
    text-decoration: none;
    color: black;
  }
  body main {
    color: #FF6060;
    gap: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  body main .codeErreur {
    width: 198px;
    height: 99px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 96px;
    font-weight: 700;
    gap: 60px;
  }
  body footer {
    background-color: black;
    color: white;
    width: 100%;
    height: 209px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  body footer .kasaLogo {
    color: white;
    width: 210px;
    height: 68px;
  }
}
@media (min-width: 992px) {
  body #root main {
    padding: 20px;
    height: auto;
  }
  body #root main header {
    width: 100%;
    display: flex;
  }
  body #root main header .conteneur-header-logo-nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  body #root main header .conteneur-header-logo-nav .kasaLogo {
    display: flex;
    width: 210px;
    height: 64px;
  }
  body #root main header .conteneur-header-logo-nav .nav-header {
    display: flex;
    width: 50%;
    gap: 20px;
    justify-content: flex-end;
  }
  body #root main header .conteneur-header-logo-nav .nav-header a {
    text-decoration: none;
    color: black;
  }
  body #root main main {
    color: #FF6060;
    gap: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  body #root main main .codeErreur {
    width: 198px;
    height: 99px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 288px;
    font-weight: 700;
    gap: 60px;
  }
}